:root{
  --bg: #458EFC;
  --bg-accent: #484a4d;
  --text-color: #fff;
  --nav-size: 60px;
  --border: 1px solid  #474a4d;
  --border-radius:  8px;
  --speed: 500ms
}


ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: var(--text-color);
  text-decoration: none;
}

.main{
    margin-top: 100px;
    display: flex;
    justify-content: center;
   
}



.julkaisut{
  display: grid;
  grid-template-columns: repeat(3, 300px);
}

.julkaisu{
  border: 1px solid black;
  height: 150px;
  margin: 20px;
  border-radius: 10%;
  display: grid;
  place-items: center;
  word-wrap: break-word;
    word-break: break-all;
    padding: 2rem;
}

.content{
  width: 100%;
}

.buttons{
  
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.contenttext{
  display: flex;
  justify-content: center;
}

.likeButton {
  color: #fd4e4e;
  height: 20px;
  width: 20px;
  transition: all .2s ease-in-out;
}

.likeButton:hover{
  transform: scale(1.2);
}

.viewButton {
  height: 20px;
  width: auto;
  transition: all .2s ease-in-out;
}
.viewButton:hover{
  transform: scale(1.1);
}



/**Navbar styiling *'*/
.nav {
  height: var(--nav-size);
  background-color: var(--bg);
  padding: 0 1rem;
  border-bottom: var(--border);
  margin-top: -80px;
  font-size: 1rem;
  position: sticky;
  top: 0;
  z-index: 10;
}
.navbarContainer{
  position: sticky;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  height: 80px;
  z-index: 1;
  justify-content: center;
  width: 100%;
  padding: 0 24px;
  max-width: 1100px;
}

.navnav{
  max-width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
}
.navitem{
  width: calc(var(--nav-size)*0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}
.iconbutton{
  --size: calc(var(--nav-size)*0.5);
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: filter 300ms;
  color: #fff;
}

.iconbutton:hover{
  background-color: #7DB1FF;
}

.dropdown {
  position: absolute;
  top: 58px;
  width: 200px;
  transform: translate(-45%);
  border-radius: var(--border-radius);
  background-color: #7DB1FF;
  padding: 1rem;
  overflow: hidden;
}
.menuitem{
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: var(--border-radius);
  transition: background var(--speed);
  padding: 0.5rem;

}

@media only screen and (max-width: 1000px) {
  .julkaisut{
    display: grid;
    grid-template-columns: repeat(2, 300px);
  }
}

@media only screen and (max-width: 850px) {
  .julkaisut{
    display: grid;
    grid-template-columns: repeat(1, 300px);
  }
}